import React from "react"

// Custom component to add easypz attribute if svg is generated by mermaid
export const SVG = (props) => {
  //console.log("===> props :: ", props)
  return (props.id && props.id.includes("mermaid-")) ? 
    <svg easypz='{"applyTransformTo": "svg > *", "modes": ["SIMPLE_PAN", "HOLD_ZOOM_IN", "DBLCLICK_ZOOM_OUT"]}' {...props}>{props.children}</svg>
    : <svg {...props}>{props.children}</svg>
}

export default SVG
